export const schema = z.object({
  id: z.number(),
  header: z.string(),
  type: z.string(),
  status: z.string(),
  target: z.string(),
  limit: z.string(),
  reviewer: z.string(),
})

// Create a separate component for the drag handle
function DragHandle({ id }: { id: number }) {
  const { attributes, listeners } = useSortable({
    id,
  })

  return (
    <Button
      {...attributes}
      {...listeners}
      variant="ghost"
      size="icon"
      className="text-muted-foreground size-7 hover:bg-transparent"
    >
      <IconGripVertical className="text-muted-foreground size-3" />
      <span className="sr-only">Drag to reorder</span>
    </Button>
  )
}

const columns: ColumnDef<z.infer<typeof schema>>[] = [
  {
    id: "icon",
    header: () => null,
    cell: () => (
      <div className="flex items-center justify-center">
        <IconCircleCheckFilled className="text-muted-foreground size-4" />
      </div>
    ),
    enableSorting: false,
    enableHiding: false,
  },
  {
    accessorKey: "header",
    header: "Empresa",
    cell: ({ row }) => {
      return <TableCellViewer item={row.original} />
    },
    enableHiding: false,
    size: 400,
  },
  {
    accessorKey: "type",
    header: "Tipo",
    cell: ({ row }) => <div className="capitalize">{row.original.type}</div>,
    size: 200,
  },
  {
    accessorKey: "status",
    header: "Status",
    cell: ({ row }) => {
      const status = row.original.status as string
      return (
        <Badge
          variant={status === "Ativa" ? "default" : "secondary"}
          className="capitalize"
        >
          {status}
        </Badge>
      )
    },
    size: 200,
  },
  {
    accessorKey: "target",
    header: "Meta",
    cell: ({ row }) => <div>{row.original.target}</div>,
    size: 200,
  },
  {
    accessorKey: "limit",
    header: "Limite",
    cell: ({ row }) => <div>{row.original.limit}</div>,
    size: 200,
  },
  {
    accessorKey: "reviewer",
    header: "Responsável",
    cell: ({ row }) => <div className="capitalize">{row.original.reviewer}</div>,
    size: 200,
  },
]

function DraggableRow({ row }: { row: Row<z.infer<typeof schema>> }) {
  const { transform, transition, setNodeRef, isDragging } = useSortable({
    id: row.original.id,
  })

  return (
    <TableRow
      data-dragging={isDragging}
      ref={setNodeRef}
      className="relative z-0 data-[dragging=true]:z-10 data-[dragging=true]:opacity-80 cursor-pointer hover:bg-muted/50 py-4"
      style={{
        transform: CSS.Transform.toString(transform),
        transition: transition,
      }}
      onClick={() => {}}
    >
      {row.getVisibleCells().map((cell) => (
        <TableCell 
          key={cell.id} 
          className={`py-4 ${cell.column.id === "header" ? "w-[40%]" : "w-[12%]"}`}
        >
          {flexRender(cell.column.columnDef.cell, cell.getContext())}
        </TableCell>
      ))}
    </TableRow>
  )
}

interface DataTableProps {
  data: {
    id: number;
    header: string;
    type: string;
    status: string;
    target: string;
    limit: string;
    reviewer: string;
  }[];
  onCompanySelect?: (id: number | null) => void;
  selectedCompany?: number | null;
}

interface RowData {
  id: number;
  header: string;
  type: string;
  status: string;
  target: string;
  limit: string;
  reviewer: string;
}

export function DataTable({ data, onCompanySelect, selectedCompany }: DataTableProps) {
  const [searchTerm, setSearchTerm] = React.useState("")

  const filteredData = data.filter((item) =>
    item.header.toLowerCase().includes(searchTerm.toLowerCase())
  )

  return (
    <div className="space-y-4">
      <div className="flex flex-col gap-4 md:flex-row md:items-center md:justify-between">
        <div className="flex flex-1 items-center space-x-2">
          <Input
            placeholder="Buscar empresa..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="h-8 w-full md:w-[200px]"
          />
        </div>
        {selectedCompany && (
          <Button
            variant="outline"
            size="sm"
            onClick={() => onCompanySelect?.(null)}
            className="h-8"
          >
            Voltar para lista
          </Button>
        )}
      </div>
      <div className="rounded-md border">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead className="w-[40%]">Empresa</TableHead>
              <TableHead className="w-[12%]">Tipo</TableHead>
              <TableHead className="w-[12%]">Status</TableHead>
              <TableHead className="w-[12%]">Meta</TableHead>
              <TableHead className="w-[12%]">Limite</TableHead>
              <TableHead className="w-[12%]">Responsável</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {filteredData.length === 0 ? (
              <TableRow>
                <TableCell colSpan={6} className="h-24 text-center">
                  Nenhum resultado encontrado.
                </TableCell>
              </TableRow>
            ) : (
              filteredData.map((item) => {
                const isSelected = selectedCompany === item.id;
                return (
                  <TableRow 
                    key={item.id}
                    className="cursor-pointer hover:bg-muted/50"
                    onClick={() => onCompanySelect?.(isSelected ? null : item.id)}
                  >
                    <TableCell className={`${isSelected ? 'font-bold' : ''}`}>
                      {item.header}
                    </TableCell>
                    <TableCell className="capitalize">{item.type}</TableCell>
                    <TableCell>
                      <Badge
                        variant={item.status === "Ativa" ? "default" : "secondary"}
                        className="capitalize"
                      >
                        {item.status}
                      </Badge>
                    </TableCell>
                    <TableCell>{item.target}</TableCell>
                    <TableCell>{item.limit}</TableCell>
                    <TableCell className="capitalize">{item.reviewer}</TableCell>
                  </TableRow>
                );
              })
            )}
          </TableBody>
        </Table>
      </div>
    </div>
  )
}

